import * as React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@mui/material/styles';
import RadioGroup, { useRadioGroup } from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import { useState } from 'react';

const StyledFormControlLabel = styled((props) => <FormControlLabel {...props} />)(
  ({ theme, checked }) => ({
    '.MuiFormControlLabel-label': checked && {
      color: theme.palette.primary.main,
    },
  }),
);

function MyFormControlLabel(props) {
  const radioGroup = useRadioGroup();

  let checked = false;

  if (radioGroup) {
    checked = radioGroup.value === props.value;
  }

  return <StyledFormControlLabel checked={checked} {...props} />;
}

MyFormControlLabel.propTypes = {
  /**
   * The value of the component.
   */
  value: PropTypes.any,
};



export default function UseRadioGroup({setRol}) {
const [value, setValue] = useState(''); 


const handleRadioChange = (event) => { 

  setValue(event.target.value);
  setRol(event.target.value)
   
    
  };
  return (
    <RadioGroup name="use-radio-group" value={value} onChange={handleRadioChange}>
      <MyFormControlLabel value="client" label="Deseo Contratar" control={<Radio />} />
      <MyFormControlLabel value="prov" label="Deseo Ofrecer mis Servicios" control={<Radio />} />
    </RadioGroup>
  );
}