import React, { useState, useEffect } from "react";
import { GoogleMap, LoadScript, Marker } from "@react-google-maps/api";

const containerStyle = {
  width: "400px",
  height: "300px",
};

function GoogleMapComponent({ setDirection }) {
  const [clickedLocation, setClickedLocation] = useState(null);
  const [center, setCenter] = useState({
    lat: -31.44178516482337,
    lng: -64.19332436437112,
  });
  useEffect(() => {
  
    //Verificar si el navegador admite geolocalización
    if ("geolocation" in navigator) {
       
       navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
           setCenter({ lat: latitude, lng: longitude });
        },
        (error) => {
          console.error("Error al obtener la ubicación:", error);
       }
       );
    } else {
      console.error("La geolocalización no está disponible en este navegador.");
     }
  }, []);

  const handleMapClick = (e) => {
    // Obtener las coordenadas donde se hizo clic
    const clickedLat = e.latLng.lat();
    const clickedLng = e.latLng.lng();

    // Crear una instancia del geocodificador
    const geocoder = new window.google.maps.Geocoder();

    // Crear un objeto LatLng
    const latLng = new window.google.maps.LatLng(clickedLat, clickedLng);

    // Realizar la geocodificación inversa
    geocoder.geocode({ location: latLng }, (results, status) => {
      if (status === "OK") {
        if (results[0]) {
          const address = results[0].formatted_address;

          setClickedLocation({
            address: address,
            cordinates: { lat: clickedLat, lng: clickedLng },
          });
          setDirection({address: address,
            cordinates: { lat: clickedLat, lng: clickedLng },});
        } else {
          console.error(
            "No se encontró ninguna dirección para estas coordenadas."
          );
        }
      } else {
        console.error(`Error de geocodificación inversa: ${status}`);
      }
    });
  };

  return (
    <LoadScript googleMapsApiKey="AIzaSyDQMpuq2hdsp3PN08_7XPGKHoPjVt3W0Ek" >
      <GoogleMap
        mapContainerStyle={containerStyle}
        center={center}
        zoom={13}
        onClick={handleMapClick} // Agrega un manejador de eventos para clics en el mapa
      >
        {clickedLocation && (
          <Marker
            position={{
              lat: clickedLocation.cordinates.lat,
              lng: clickedLocation.cordinates.lng,
            }}
          />
        )}
      </GoogleMap>
    </LoadScript>
  );
}

export default GoogleMapComponent;
