import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { loginOut, selectAuth } from "../features/auth/authSlice";
import SearchIcon from '@mui/icons-material/Search'
import {Search,SearchIconWrapper,StyledInputBase } from './SearchImput'

export default function ButtonAppBar() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { loggedIn, user } = useSelector(selectAuth);
  
  const handleClick = () => {
    navigate("/login", {
      // replace: true,
    });
  };

  const handleClickLogout = () => {
    dispatch(loginOut());
    navigate("/", {
      // replace: true,
    });
  };
    
    return (
      <Box sx={{ flexGrow: 1}}>
        <AppBar position="static" sx={{   background: "#FFD433" }}>
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
            >
              <MenuIcon />
            </IconButton> 
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
             {loggedIn ? "Hola " + user.name : "Te estamos esperando!!!"} 
            </Typography>
            <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search…"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
            {!loggedIn ? <Button onClick={handleClick} color="inherit">Login</Button> : 
             <Button onClick={handleClickLogout} color="inherit">Logout</Button>  }
          </Toolbar>
        </AppBar>
      </Box>
    );
  }