import { useState } from "react";
import {
  Container,
  Grid,
  Typography,
  Button,
  TextField,
  Stepper,
  Step,
  StepLabel,
} from "@mui/material";
import UseRadioGroup from "./checkBoxRol";
import GoogleMapComponent from "./MapComponent";
import StepContent from "@mui/material/StepContent";
import Copyright from "../Copyright";

const steps = [
  "¿Que deseo hacer",
  "Mi datos de registro",
  "Datos Personales",
  "Direccion",
];

const MultiStepForm = () => {
  const [activeStep, setActiveStep] = useState(0);
  const [formData, setFormData] = useState({});
  const [passwordError, setPasswordError] = useState(false);

  const handleNext = () => {
    if (activeStep === steps.length - 1) console.log(formData);
    else {
      if (activeStep === 1)
        if (formData.password !== formData.confirmPassword) {
          setPasswordError(true);
          return; // Evitar el envío si las contraseñas no coinciden
        }

      setActiveStep((prevActiveStep) => prevActiveStep + 1);
    }
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const setRol = (rol) => {
    setFormData({
      ...formData,
      ["rol"]: rol,
    });
  };

  const setDirection = (direction) => {
    setFormData({
      ...formData,
      ["direction"]: direction,
    });
  }

  const handleChange = (event) => {
    setFormData({
      ...formData,
      [event.target.name]: event.target.value,
    });
    setPasswordError(false);
  };

  return (
    <Container maxWidth="sm" sx={{ height: "50vh" }}>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
            <StepContent>
              {activeStep === 0 && (
                <>
                  <UseRadioGroup setRol={setRol} />
                </>
              )}
              {activeStep === 1 && (
                <>
                  <TextField
                    margin="normal"
                    required
                    fullWidth
                    id="email"
                    label="Email Address"
                    name="email"
                    autoComplete="email"
                    autoFocus
                    onChange={handleChange}
                  />
                  <TextField
                    margin="normal"
                    required
                    fullWidth
                    name="password"
                    label="Password"
                    type="password"
                    id="password"
                    autoComplete="current-password"
                    onChange={handleChange}
                  />
                  <TextField
                    margin="normal"
                    required
                    fullWidth
                    name="confirmPassword"
                    label="Confirmar Password"
                    type="password"
                    id="confirmPassword"
                    onChange={handleChange}
                    error={passwordError} // Mostrar error si las contraseñas no coinciden
                    helperText={passwordError && "Las contraseñas no coinciden"}
                  />
                </>
              )}
              {activeStep === 2 && (
                <>
                  <Typography variant="h6">Step 3</Typography>
                  <TextField
                    label="Nombre"
                    name="name"
                    onChange={handleChange}
                    fullWidth
                    margin="normal"
                  />
                  <TextField
                    label="Apellido"
                    name="lastname"
                    onChange={handleChange}
                    fullWidth
                    margin="normal"
                  />
                  <TextField
                    id="date"
                    label="Fecha de Nacimiento"
                    type="date"
                    onChange={handleChange}
                    InputLabelProps={{ shrink: true }}
                  />
                </>
              )}
              <div>
              {activeStep > 0 && activeStep < 3 && (
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleBack}
                  sx={{ mt: 3, mb: 2, background: "#FFD433", color: "black" }}
                >
                  Back
                </Button>
              )}
              {activeStep < 3 && (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleNext}
                  //disabled={activeStep === steps.length - 1}
                  sx={{ mt: 3, mb: 2, background: "#FFD433", color: "black" }}
                >
                  {activeStep === steps.length - 1 ? "Submit" : "Next"}
                </Button>
              )}
              </div>
            </StepContent>
          </Step>
        ))}
      </Stepper>

      {activeStep === 3 && (<GoogleMapComponent setDirection={setDirection}/>)}
      {activeStep === 3 && (
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleBack}
                  sx={{ mt: 3, mb: 2, background: "#FFD433", color: "black" }}
                >
                  Back
                </Button>
              )}
              {activeStep === 3 && (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleNext}
                  //disabled={activeStep === steps.length - 1}
                  sx={{ mt: 3, mb: 2, background: "#FFD433", color: "black" }}
                >
                  {activeStep === steps.length - 1 ? "Submit" : "Next"}
                </Button>
              )}
   
   <Copyright sx={{ mt: 5 }} />
    </Container>
  );
};

export default MultiStepForm;
